import { FEE_RECEIPT_STATUS, FEE_RECEIPT_STATUS_LABEL } from "../../../utils/Constant";

export const getReceiptStatusLabel = (status: string) => {
  switch (status) {
    case FEE_RECEIPT_STATUS.PENDING_DEPOSIT:
      return FEE_RECEIPT_STATUS_LABEL.PENDING_DEPOSIT;
    case FEE_RECEIPT_STATUS.DEPOSITED:
      return FEE_RECEIPT_STATUS_LABEL.DEPOSITED;
    case FEE_RECEIPT_STATUS.CLEARED:
      return FEE_RECEIPT_STATUS_LABEL.CLEARED;
    case FEE_RECEIPT_STATUS.BOUNCED:
      return FEE_RECEIPT_STATUS_LABEL.BOUNCED;
    case FEE_RECEIPT_STATUS.INVALID:
      return FEE_RECEIPT_STATUS_LABEL.INVALID;
    default:
      return "";
  }
};
